.header {
  text-align: center;
  padding-bottom: 2em;
  color: #fff;
  background-color: #e8e8e8; }

.header .header__row {
  padding-top: 1.4em;
  padding-bottom: 1.4em; }

.header--primary {
  padding-top: 2em;
  color: #2c3e50; }

.header--secondary {
  font-size: 0.9em;
  padding: 2em 0 4em;
  background-color: #42A5F5; }

.header--secondary .grid__cell {
  padding: 1em; }

.header--inverted {
  background-color: #222; }

.header--white {
  color: inherit;
  background-color: #fff; }

.logo .o-image {
  display: inline-block;
  width: 200px; }

.product-name {
  font-size: 3.5em;
  font-weight: bold; }

@media (min-width: 42em) {
  .product-name {
    font-size: 5em; } }

.product-name span {
  font-weight: normal; }

.hero-button {
  letter-spacing: 4px; }

/************************************************

Stylesheet: base stylesheet

*************************************************/
s
body, h1, h2, h3, h4, h5, h6,
p, blockquote, pre, hr,
dl, dd, ol, ul, figure {
  margin: 0;
  padding: 0; }

/**
 * Basic styling
 */
body {
  font: 400 1em/1.5 -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  color: black;
  background-color: #fdfdfd;
  -webkit-text-size-adjust: 100%;
  -webkit-font-feature-settings: "kern" 1;
  -moz-font-feature-settings: "kern" 1;
  -o-font-feature-settings: "kern" 1;
  font-feature-settings: "kern" 1;
  font-kerning: normal; }

/**
 * Set `margin-bottom` to maintain vertical rhythm
 */
h1, h2, h3, h4, h5, h6,
p, blockquote, pre,
ul, ol, dl, figure {
  margin-bottom: 15px; }

/**
 * Images
 */
img {
  max-width: 100%;
  vertical-align: middle; }

/**
 * Figures
 */
figure > img {
  display: block; }

figcaption {
  font-size: 0.875em; }

/**
 * Lists
 */
li > ul,
li > ol {
  margin-bottom: 0; }

/**
 * Headings
 */
h1, h2, h3, h4, h5, h6 {
  font-weight: normal; }

/**
 * Links
 */
a {
  color: #2a7ae2;
  text-decoration: none; }
  a:visited {
    color: #1756a9; }
  a:hover {
    color: #111;
    text-decoration: underline; }

/**
 * Blockquotes
 */
blockquote {
  color: #828282;
  border-left: 4px solid #e8e8e8;
  padding-left: 15px;
  font-size: 18px;
  letter-spacing: -1px;
  font-style: italic; }
  blockquote > :last-child {
    margin-bottom: 0; }

/**
 * Code formatting
 */
pre,
code {
  font-size: 15px;
  border: 1px solid #e8e8e8;
  border-radius: 3px;
  background-color: #eef; }

code {
  padding: 1px 5px; }

pre {
  padding: 8px 12px;
  overflow-x: auto; }
  pre > code {
    border: 0;
    padding-right: 0;
    padding-left: 0; }

/**
 * Clearfix
